import React, {useState, useEffect} from 'react'
import axios from 'axios';

function SampleCustomer() {
    const url = "http://localhost:9900/binary-bomber-service/customer?customerId="+ this.state.customerId;
    const [customer, setCustomer] = useState(null)

    useEffect(()=>{
        axios.get(url)
        .then(response => {
            setCustomer(response.data)
        })
    }, [url]) 
    if (customer){
        return (
            <div>
                <h1> CUSTOMER </h1>
                <h2>customer.id</h2>
            </div>
        )
    }
    
}

export default SampleCustomer
